<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`CleanImportLoanOutstanding${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import CleanImportLoanOutstandingListLayout from '@/views/trade/loan/clean-import-loan/clean-import-loan-outstanding/clean-import-loan-outstanding-browse/CleanImportLoanOutstandingListLayout'
import CleanImportLoanOutstandingCardLayout from '@/views/trade/loan/clean-import-loan/clean-import-loan-outstanding/clean-import-loan-outstanding-browse/CleanImportLoanOutstandingCardLayout'
import CleanImportLoanOutstandingPivotLayout from '@/views/trade/loan/clean-import-loan/clean-import-loan-outstanding/clean-import-loan-outstanding-browse/CleanImportLoanOutstandingPivotLayout'
import CleanImportLoanOutstandingTableMixin from '@/mixins/trade/loan/CleanImportLoanOutstandingTableMixin'

export default {
  name: 'clean-import-loan-outstanding-browse',
  mixins: [CleanImportLoanOutstandingTableMixin],
  components: {
    CleanImportLoanOutstandingListLayout,
    CleanImportLoanOutstandingCardLayout,
    CleanImportLoanOutstandingPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadLoanTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
